<template>
    <div>
        <!-- 在线客服 -->
        <van-tabbar style="margin-left: 30px; margin-bottom: 20px;">
            <form >
            <van-search
                v-model="value"
                show-action
                placeholder="请输入搜索关键词"
                @search="onSearch"
                >
                <template #action>
                    <div @click="onSearch">发送</div>
                </template>
                </van-search>
            </form>
        </van-tabbar>
    </div>
</template>

<script>
import { Toast } from 'vant';

export default {
    data() {
        return {
            value: '',
        };
    },
    methods: {
        onSearch(e) {
            if(!this.value) return;
            Toast.fail('功能尚未开通');
            setTimeout(()=> {
                this.value = "";
            }, 300)
        },
    },
};
</script>

